export default {
    data() {
        return {
            menuIndex: ''
        }
    },
    methods: {
        showMenu(vipindex) {
            this.menuIndex = 'vip-' + vipindex;
        },
        clickMenuItem(vipindex, item, name) {
            this.menuIndex = '';
            this.$emit('click', {
                type: 'menu-item',
                name: name,
                detail: {
                    vipIndex: vipindex,
                    ...item
                }
            })
        },
        clickVip(item) {
            this.$emit('click', {
                type: 'vip',
                detail: {
                    val: item
                }
            })
        },
